gn:center;position:relative}.weather-info .weather-card-header::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:70px;background:linear-gradient(to top, rgba(0, 0, 0, 0.1), transparent);z-index:1}.weather-info .city-name{font-size:32px;font-weight:700;margin-bottom:15px;text-shadow:0 2px 4px rgba(16,42,67,.2);letter-spacing:.5px}.weather-info .weather-card-body{padding:40px;background-color:hsla(0,0%,100%,.99)}.weather-info .weather-param{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:25px 20px;background-color:#f0f4f8;border-radius:12px;transition:all .2s ease;animation:slideRight .6s ease-out;box-shadow:0 4px 12px rgba(16,42,67,.03);border:1px solid rgba(44,107,237,.03)}.weather-info .weather-param:hover{transform:translateY(-4px);box-shadow:0 10px 25px rgba(16,42,67,.1);background-color:#fff}.weather-info .weather-param svg{color:#2c6bed;width:28px;height:28px;margin-bottom:12px;opacity:.9}.weather-info .weather-icon{width:140px;height:140px;margin:10px auto 15px;filter:drop-shadow(0 10px 20px rgba(16, 42, 67, 0.2));position:relative;z-index:2}.weather-info .temperature{font-size:72px;font-weight:700;margin-bottom:10px;line-height:1;text-shadow:0 4px 12px rgba(16,42,67,.15);position:relative;z-index:2}.weather-info .weather-description{font-size:20px;text-transform:capitalize;font-weight:500;opacity:.9;letter-spacing:.5px;position:relative;z-index:2}.weather-info .additional-info{display:grid;grid-template-columns:repeat(2, 1fr);gap:25px;margin-top:30px;font-size:16px;animation:fadeInUp 1.8s ease-out}.weather-info .additional-info p{background:linear-gradient(135deg, rgba(0, 131, 176, 0.1), rgba(0, 180, 219, 0.1));padding:15px;border-radius:10px;width:100%;width:45%;box-shadow:0 3px 10px rgba(0,0,0,.05);transition:all .3s ease;border:1px solid rgba(0,131,176,.1)}.weather-info .additional-info p:hover{transform:translateY(-3px);box-shadow:0 5px 15px rgba(0,0,0,.1)}.error{color:#ff5252;margin-top:15px;margin