*{box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;margin:0;padding:0;width:100%;overflow-x:hidden;font-family:"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell",sans-serif;line-height:1.5;scroll-behavior:smooth}body{background:linear-gradient(160deg, #2c6bed, #03A9F4);background-attachment:fixed;margin:0;padding:0;min-height:100vh;position:relative;color:#334e68;font-size:16px}body::before{content:"";position:fixed;top:0;left:0;width:100%;height:100%;background-image:url("https://images.unsplash.com/photo-1534088568595-a066f410bcda?ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80");background-size:cover;background-position:center;opacity:.1;z-index:-1}body::after{content:"";position:fixed;bottom:0;left:0;width:100%;height:40%;background:linear-gradient(to top, rgba(0, 0, 0, 0.05), transparent);z-index:-1}.site-header{background-color:hsla(0,0%,100%,.98);backdrop-filter:blur(10px);box-shadow:0 4px 20px rgba(16,42,67,.1);padding:22px 0;position:sticky;top:0;width:100%;z-index:1000;margin-bottom:0}.site-header .header-content{max-width:1600px;margin:0 auto;padding:0 40px;display:flex;justify-content:space-between;align-items:center}.site-header .header-content .logo{display:flex;align-items:center;font-size:28px;font-weight:700;color:#2c6bed;text-decoration:none}.site-header .header-content .logo svg{width:42px;height:42px;margin-right:12px;fill:#2c6bed}.site-header .header-content .nav-links{display:flex;gap:35px}.site-header .header-content .nav-links a{color:#102a43;text-decoration:none;font-weight:500;font-size:17px;transition:all .3s ease;position:relative;padding-bottom:5px}.site-header .header-content .nav-links a::after{content:"";position:absolute;bottom:0;left:0;width:0;height:2px;background-color:#2c6bed;transition:width .3s ease}.site-header .header-content .nav-links a:hover{color:#2c6bed}.site-header .header-content .nav-links a:hover::after{width:100%}.site-header .header-content .nav-links a